.upload-btn-wrapper {
    z-index: 100;
    position: relative;
    overflow: hidden;
    display: inline-block;
    transition: background 0.2s ease-in,
                color 0.2s ease-in; 
    
 

    &:hover .btn {
        // background: #0e813e;
        background: #f3f3f3;
        color: #0ea44d;
    }
  }
  
  .btn {
    // border: 2px solid gray;
    border: none;
    color: #f3f3f3;
    background: #0ea44d;
    padding: 3rem 4rem;
    border-radius: 8px;
    font-size: 2.5rem;
    font-weight: bold;
    transition: background 0.2s ease-in,
    color 0.2s ease-in; 
    text-shadow: 5px 2px 2px #00000025;
  }
  
  .upload-btn-wrapper input[type=file] {
    font-size: 10rem;
    position: absolute;
    left: 0;
    top: 0;
    opacity: 0;
    cursor: pointer;
  }



.dropzone {
  min-height: 150px;
  width: 40rem;
  background: #0ea44d;
  padding: 20px 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  border-radius: 8px;

  & p {
    font-size: 2.2rem;
    font-weight: bold;
    color: white;
    text-shadow: 2px 2px 15px #00000025;
    font-family: 'Quicksand', sans-serif;
    font-weight: 400;

    & span {
      color: #67fba5;
      cursor: pointer;
      text-shadow: 5px 5px 4px #00000025;
    }
  }
}


select {
  width: 80%;
  height: 50%;
  background-color: #17ca61;
  border: none;
  font-size: 2rem;
  text-align-last: center;
  font-family: 'Quicksand', sans-serif;
  font-weight: bold;
  color: #3b3838c3;
  letter-spacing: 0.5px;
  text-shadow: 1px 1px 3px #00000025;
  
}

  